.container {
  /* background-color: #f7f7f7; */
  padding: 0px 0px 20px 0px;
  width: 100%;
}

.content {
  font-family: Helvetica, Arial, Helvetica Neue, sans-serif;
  font-size: 16px;
  line-height: 1.6;
  -webkit-tap-highlight-color: transparent;
  box-sizing: content-box;
  display: block;
  width: 1120px;
  background-color: #fff;
  margin: 0 auto;
  padding: 32px 40px 0;
  width: 80%;
}

.main-box-inside {
  line-height: 1.6;
  -webkit-tap-highlight-color: transparent;
  margin: auto;
  /* padding: 0; */
  /* display: block; */
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
}

.content-header {
  box-shadow: 0 1px 3px 0 rgb(0 0 0 / 10%);
  padding: 15px;
  display: flex;
  justify-content: center;
}

.box-con {
  -webkit-text-size-adjust: 100%;
  font-family: Helvetica, Arial, Helvetica Neue, sans-serif;
  font-size: 16px;
  line-height: 1.6;
  -webkit-tap-highlight-color: transparent;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  align-items: stretch;
  align-self: flex-start;
  color: #000;
  display: inline-flex;
  flex-direction: column;
  overflow: hidden;
  text-decoration: none;
  vertical-align: top;
  width: 336px;
  background-color: #fff;
  margin: 10px;
}

.box-img-link {
  font-size: 16px;
  line-height: 1.6;
  -webkit-tap-highlight-color: transparent;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  background-color: transparent;
  color: #000;
  align-items: stretch;
  display: flex;
  flex-shrink: 0;
  height: 177px;
  overflow: hidden;
  border: 1px solid rgba(205, 209, 218, 0.4);
  border-radius: 12px;
}

.box-img-div {
  font-size: 16px;
  line-height: 1.6;
  -webkit-tap-highlight-color: transparent;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  width: 100%;
}

.box-img-div > img {
  -webkit-text-size-adjust: 100%;
  font-size: 16px;
  line-height: 1.6;
  -webkit-tap-highlight-color: transparent;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border-style: none;
  display: block;
  width: 100%;
}

