.icons-con {
  -webkit-text-size-adjust: 100%;
  font-family: Helvetica, Arial, Helvetica Neue, sans-serif;
  font-size: 16px;
  line-height: 1.6;
  color: #000;
  -webkit-tap-highlight-color: transparent;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  display: flex;
  height: 32px;
  flex-grow: 1;
  flex-shrink: 0;
  margin-bottom: 4px;
  margin-top: 8px;
}

.icon {
  -webkit-text-size-adjust: 100%;
  font-family: Helvetica, Arial, Helvetica Neue, sans-serif;
  -webkit-tap-highlight-color: transparent;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  align-items: center;
  color: #000;
  justify-content: center;
  text-decoration: none;
  display: flex;
  flex-basis: 0;
  flex-grow: 1;
  font-size: 12px;
  line-height: 16px;
  background-color: #f3f3f6;
  border-radius: 8px;
  margin-right: 8px;
}

.icon > span {
  -webkit-text-size-adjust: 100%;
  font-family: Helvetica, Arial, Helvetica Neue, sans-serif;
  color: #000;
  font-size: 12px;
  line-height: 16px;
  -webkit-tap-highlight-color: transparent;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  margin-right: 0;
}

.icon-svg {
  -webkit-text-size-adjust: 100%;
  font-family: Helvetica, Arial, Helvetica Neue, sans-serif;
  color: #000;
  font-size: 12px;
  line-height: 16px;
  -webkit-tap-highlight-color: transparent;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  display: flex;
  margin-right: 4px;
}

.icon-svg > svg {
  -webkit-text-size-adjust: 100%;
  font-family: Helvetica, Arial, Helvetica Neue, sans-serif;
  color: #000;
  font-size: 12px;
  line-height: 16px;
  -webkit-tap-highlight-color: transparent;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  height: 16px;
  width: 16px;
}

.icon-svg > svg > use {
  -webkit-text-size-adjust: 100%;
  font-family: Helvetica, Arial, Helvetica Neue, sans-serif;
  color: #000;
  font-size: 12px;
  line-height: 16px;
  -webkit-tap-highlight-color: transparent;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.box-title {
  font-family: Helvetica, Arial, Helvetica Neue, sans-serif;
  line-height: 1.6;
  color: #000;
  -webkit-tap-highlight-color: transparent;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  margin-top: 20px;
}

.box-title > a {
  font-family: Helvetica, Arial, Helvetica Neue, sans-serif;
  -webkit-tap-highlight-color: transparent;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  background-color: transparent;
  color: #000;
  display: flex;
  flex-grow: 0;
  flex-shrink: 1;
  font-size: 20px;
  line-height: 28px;
  text-decoration: none;
}

.icon > .title {
  margin-left: 3px;
}

@media screen and (max-width: 960px) {
  .content {
    width: 90%;
    padding: 32px 10px 0;
  }

  .main-box-inside {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .box-con {
    width: 100%;
    max-width: 306px;
    margin: auto;
    margin-bottom: 32px;
  }
}


