  -webkit-font-smoothing: antialiased;
  box-sizing: border-box;
  margin: 0;
  min-width: 0;
  font-family: 'Inter',sans-serif;
  line-height: 1.33;
  display: block;
  color: #2d3748;
  font-weight: bold;
  text-decoration: none;
  font-size: 1.5rem;
  position: relative;
  margin-bottom: 0;
  padding-left: 2rem;
  margin-left: 0;
  margin-right: 0;
}
.css-vkk9sv::before {
  display: block;
  background-color: var(--theme-ui-colors-omegaLight,#e2e8f0);
  content: "";
  border-radius: 9999px;
  position: absolute;
  height: 100%;
  width: 5px;
  top: 0;
  left: 0;
}